.list{
    color:white;
    width: 60%;
    background-color: black;
    height: 50px;
    margin: 20px auto;
    border-radius: 10px;
}

.list p{
    padding: 10px;
    position: relative;
}

.list p span{
    right:30px;
    position: absolute;
}

.list p input{
    background-color: transparent;
    border:0;
    color: white;
    font-size: 16px;
}

.list p input:focus{
   outline: none; 
}